<template>
  <div class="registered_page">
    <!-- <div class="pages_tips">&nbsp;&gt;&nbsp;<b></b></div> -->
    <div class="rules_desc" v-if="canEdit">
      <h3></h3>
      <p class="rules_tips">GroupyGroupy</p>
      <el-form label-width="130px" :rules="rules" :model="forms" ref="forms">
        <!-- <p class="forms_tips"></p> -->
        <el-form-item label="" prop="bank">
          <el-input placeholder="UFJ" v-model="forms.bank"></el-input>
        </el-form-item>
        <el-form-item label="" prop="branch">
          <el-input placeholder="" v-model="forms.branch"></el-input>
        </el-form-item>
        <el-form-item label="" prop="depositType">
          <el-select v-model="forms.depositType">
            <el-option
              key="ordinary"
              label=""
              value="ordinary">
            </el-option>
            <el-option
              key="current"
              label=""
              value="current">
            </el-option>
            <el-option
              key="savings"
              label=""
              value="savings">
            </el-option>
            <el-option
              key="others"
              label=""
              value="others">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="accountNumber">
          <el-input placeholder="1234567" type="number" v-model="forms.accountNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="accountName">
          <el-input placeholder="" v-model="forms.accountName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="accountNameHurigana">
          <el-input placeholder="" v-model="forms.accountNameHurigana"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" v-model="forms.agentName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" v-model="forms.organization"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input placeholder="" v-model="forms.telephone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input placeholder="" v-model="forms.email"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" v-model="forms.city"></el-input>
        </el-form-item>
        <div class="privacy_rule">
          <h4>Groupy</h4>
          <p style="padding-left: 0;text-indent: 12px;">NewsJetGroupy for idol</p>
          <h5></h5>
          <p>
            1GroupyGroupy for idol<br>2<br>3Groupy
          </p>
          <h5>2</h5>
          <p>
            110035<br>2252000
          </p>
          <h5>3</h5>
          <p>
            1<br>2

          </p>
          <h5>4</h5>
          <p>
            1<br><i>wanted@groupy.cn</i>
          </p>
          <h5>5</h5>
          <p>
            1<br>2
          </p>
          <h5>6</h5>
          <p>1</p>
        </div>
        <!-- <p class="forms_tips"></p> -->
        <div class="protocol_content">
          <div class="button_content" style="margin-bottom: 20px;"><img :src="protocol_img" @click="change_protocol()"><span :class="{error: !protocol}">Groupy</span></div>
        </div>
        <div class="button_content">
          <el-button type="primary" size="large" class="submit" @click="submitForm('forms')">&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
        </div>
      </el-form>
    </div>
    <div class="rules_desc" v-if="canEdit == false">
      <h3></h3>
      <el-form label-width="130px" :model="forms" ref="forms">
        <!-- <p class="forms_tips"></p> -->
        <el-form-item label="">
          <el-input placeholder="UFJ" v-model="forms.bank" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" v-model="forms.branch" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="forms.depositType" :disabled="true">
            <el-option
              key="ordinary"
              label=""
              value="ordinary">
            </el-option>
            <el-option
              key="current"
              label=""
              value="current">
            </el-option>
            <el-option
              key="savings"
              label=""
              value="savings">
            </el-option>
            <el-option
              key="others"
              label=""
              value="others">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="1234567" type="number" min="0" :disabled="true" v-model="forms.accountNumber"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.accountName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.accountNameHurigana"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.agentName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.organization"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.telephone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.email"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder="" :disabled="true" v-model="forms.city"></el-input>
        </el-form-item>
        <div class="button_content">
          <el-button type="primary" size="large" class="submit" @click="canEdit = true">&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
        </div>
      </el-form>
    </div>
    <div class="login_sns" v-if="Havedlogin">
      <div class="login_content">
        <div class="login_tips"></div>
        <div class="api_login" @click="twitterLogin()"><img src="http://photodebug.oss-cn-hongkong.aliyuncs.com/h5_groupy/login/icon_twitter.png"><span>Twitter</span></div>
        <div class="api_login facebook" @click="facebookLogin()"><img src="http://photodebug.oss-cn-hongkong.aliyuncs.com/h5_groupy/login/icon_facebook.png"><span>Facebook</span></div>
      </div>
      <div class="loading" v-if="loading">
        <i class="el-icon-loading"></i>
      </div>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      size="tiny">
      <span>{{successTips}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import http from '../../js/http.js'
  export default {
    data() {
      return {
        forms: {
          email: '',
          telephone: '',
          bank: '',
          branch: '',
          accountNumber: '',
          accountName: '',
          accountNameHurigana: '',
          depositType: 'ordinary',
          city: '',
          agentName: '',
          organization: '',
        },
        rules: {
          bank: [{ required: true, message: '', trigger: 'blur' }],
          branch: [{ required: true, message: '', trigger: 'blur' }],
          accountNumber: [{ required: true, message: '' }],
          accountName: [{ required: true, message: '', trigger: 'blur' }],
          accountNameHurigana: [{ required: true, message: '', trigger: 'blur' }],
          telephone: [{ required: true, message: '', trigger: 'blur' }],
          depositType: [
            { required: true, message: '', trigger: 'change' }
          ],
          email: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur,change' }
          ]
        },
        protocol_img: 'http://photodebug.oss-cn-hongkong.aliyuncs.com/h5_groupy/agree/agree.png',
        protocol: true,
        Havedlogin: false,
        dialogVisible: false,
        tokens: '',
        successTips: '',
        canEdit: true,
        loading: false
      }
    },
    methods: {
      change_protocol() {
          this.protocol = !this.protocol;
          if(this.protocol == true) {
              this.protocol_img= 'http://photodebug.oss-cn-hongkong.aliyuncs.com/h5_groupy/agree/agree.png'
          }else {
              this.protocol_img= 'http://photodebug.oss-cn-hongkong.aliyuncs.com/h5_groupy/agree/disagree.png'
          }
      },
      facebookLogin() {
        var self= this;
        self.loading = true;
        window.fbAsyncInit = function() {
            FB.init({
              appId            : '310622289379360',
              autoLogAppEvents : true,
              xfbml            : true,
              version          : 'v2.10'
            });
            // FB.AppEvents.logPageView();
            FB.getLoginStatus(function(response) {
              console.log(response)
            if (response.status === 'connected' || response.status === 'not_authorized') {
              console.log(response);
              FB.api('/me', {fields: 'name'}, function(response) {
                self.login(response.id,'fb');
              });
            } else {
              FB.login(function(response) {
                FB.api('/me', {fields: 'name'}, function(response) {
                    console.log(response);
                    // var obj = {
                    //     snsUid:response.id,
                    //     snsPlatform:'fb',
                    //     nickname:response.name,
                    //     avatar:'https://graph.facebook.com/'+ response.id +'/picture?type=large', 
                    //     sign:'',
                    //     introduce:''
                    // };
                    self.login(response.id,'fb');
                });
              }, {scope: 'public_profile'});
            }
          });
        };
        (function(d, s, id){
             var js, fjs = d.getElementsByTagName(s)[0];
             if (d.getElementById(id)) {return;}
             js = d.createElement(s); js.id = id;
             js.src = "http://connect.facebook.net/en_US/sdk.js";
             fjs.parentNode.insertBefore(js, fjs);
        }(document, 'script', 'facebook-jssdk'));
      },
      twitterLogin() {
        var self = this;
        self.loading = true;
        console.log(self.loading);
        window.twttr = (function(d, s, id) {
             var js, fjs = d.getElementsByTagName(s)[0],
              t = window.twttr || {};
              if (d.getElementById(id)) return t;
              js = d.createElement(s);
              js.id = id;
              js.src = "https://platform.twitter.com/widgets.js";
              fjs.parentNode.insertBefore(js, fjs);

              t._e = [];
              t.ready = function(f) {
                  t._e.push(f);
              };
              return t;
        }(document, "script", "twitter-wjs"));
        var log = console.log;
        hello.init({'twitter': 'LE49JopVjaqLB3xtzOQdQtP3n'},{
          oauth_proxy: 'https://auth-server.herokuapp.com/proxy' 
        });
        function login_twitter(network){  //twitter 
              // Twitter instance
              var twitter = hello(network);
              // Login
              twitter.login().then(function(r){
                  // Get Profile
                  return twitter.api('/me');
              },log).then(function(p){
                console.log(p)
                self.login(p.id,'tw');
              }, log );
        }
        login_twitter('twitter');
      },
      login(snsid,pf) {
        var self = this;
        http.post('/groupyuser/loginThirdpart?tokenize=true&snsid='+ snsid + '&snspf=' + pf).then(function(res){
            if(res.status == 200) {
                self.tokens = res.data.accessToken;
                self.getInfo();
                console.log(self.tokens);
            }
        }).catch(function(err){
            self.loading = false;
            self.$message.error(err.data.errorMsg);
            setTimeout(function(){
              location.href = location.href;
            },1000)
            
        });
      },
      submitForm(formName) {
        let self = this;
        self.$refs[formName].validate((valid) => {
          if (valid && self.protocol) {
            http.defaults.headers.common['Authorization'] = 'Token '+ self.tokens;
            http.post('/groupyuser/applyIdolBanking',JSON.stringify(self.forms)).then(function(res){
              if(res.status == 200) {
                self.successTips = '';
                self.dialogVisible = true;
                self.canEdit = false;
              }else {
                // self.dialogVisible = true;
                // self.successTips = ""
                self.$message.error('');
              }
              console.log(res);
            }).catch(function(err){
              console.log(err);
              self.$message.error('');
              // self.dialogVisible = true;
              // self.successTips = err.data.errorMsg;
            });
          } else {
            return false;
          }
        });
      },
      getInfo() {
        var self = this;
        http.defaults.headers.common['Authorization'] = 'Token '+ self.tokens;
          http.get('/groupyuser/idolBanking').then(function(res){
            console.log(res);
            self.loading = false;
            self.Havedlogin = false;
            if(res.status == 200) {
              if(res.data.id) {
                self.forms = res.data;
                self.canEdit = false;
              }
            }else {
              self.$message.error(res.errorMsg);
            }
          }).catch(function(err){
            self.loading = false;
            console.log(err);
            self.$message.error(err.data.errorMsg);
            // self.dialogVisible = true;
            // self.successTips = err.data.errorMsg;
          });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/css">
  .el-input.is-disabled .el-input__inner {
    color: #666 !important;
  }
</style>
<style  rel="stylesheet/scss" lang="scss" scoped>
  .registered_page {
    background: #f7f7f7;
    text-align: left;
    color: #666666;
    min-height: 100vh;
    overflow: hidden;
  }
  .protocol_error {
    left: 70%;
    top: 4px;
  }
  .forms_tips {
    font-size: 10px;
    color: #ff4949;
    -webkit-transform: scale(0.8);

  }
  .el-select {
    width: 100% !important;
  }
  .button_content {
    text-align: center;
    img {
      width: 15px;
      margin-right: 5px;
      vertical-align: middle;
      cursor: pointer;
    }
    span {
      display: inline-block;
      vertical-align: middle;
      font-size: 12px;
      cursor: pointer;
      color: #48576a;
    }
    span.error {
      color: #ff4949;
    }
    .submit {
      width: 250px;
      height: 50px;
      background: #FC4083;
      border-color: #FC4083;
      font-size: 18px;
    }
  }
  .rules_desc {
    padding: 30px 100px;
    max-width: 600px;
    margin: 40px auto;
    background: #fafafa;
    box-shadow: 0 2px 4px 2px rgba(208, 208, 208,0.5);
    border-radius: 6px;
    h3 {
      font-size: 24px;
      font-weight: 600;
      text-align: center;
      margin-bottom: 20px;
      color: #333;
    }
    .rules_tips {
      font-size: 12px;
      margin-bottom: 20px;
    }
  }
  .protocol_content {
    position: relative;
  }
  .privacy_rule {
    max-height: 250px;
    overflow: auto;
    background: #fff;
    font-size: 12px;
    // box-shadow: 0 0 4px 4px rgba(208, 208, 208,0.5);
    border: 1px solid #bfcbd9;
    border-radius: 6px;
    margin-bottom: 10px;
    padding: 12px;
    h4 {
      text-align: center;
      font-weight: 600;
      font-size: 13px;
      padding-bottom: 5px;

    }
    h5 {
      font-weight: 600;
      padding: 3px 0;
    }
    p {
      padding-left: 12px;
    }
  }
  .login_sns {
    position: absolute;
    left: 0;right: 0;top: 0;bottom: 0;
    background: rgba(0,0,0,0.8);
    .login_content {
      background: #fafafa;
      position: absolute;
      left: 50%;top: 50%;
      margin-top: -120px;
      margin-left: -201px;
      border-radius: 6px;
      border: 1px solid #eee;
      padding-top: 20px;
      padding-bottom: 40px;
      .login_tips {
        padding-bottom: 20px;
        padding-left: 20px;
        font-size: 18px;
      }
      .api_login {
        height: 30px;
        line-height: 30px;
        background: #009ef4;
        padding: 12px 0 13px;
        border-radius: 39px;
        margin: 0 50px;
        color: #fff;
        font-size: 18px;
        font-weight: 300;
        text-align: center;
        width: 300px;
        cursor: pointer;
        img {
          height: 35px;
          vertical-align: middle;
        }
        span {
          display: inline-block;
          vertical-align: middle;
        }
      }
      .facebook {
          background: #33589d;
          margin-top: 20px;
      }
    }
    .loading {
      position: absolute;
      left: 50%;top: 50%;
      margin-top: -120px;
      margin-left: -201px;
      background: #fafafa;
      width: 402px;height: 239px;
      box-sizing: border-box;
      padding-top: 80px;
      padding-left: 176px;
      border-radius: 6px;
      i {
        font-size: 50px;
      }
    }
  }
</style>



